import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, downloadCSVFromJson, isSuperUser, updStore, ObjectKeys, csvJSON, qryStore, fetchEncryptStream } from "../../../stores/Utils";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt, SearchInput_Cmpt, CheckBoxSlide_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";
import { prepareSubMayorDatatoCvs, compareEstadoCuenta, calcSubmayor, sortComprobantesByDate, compareBanks, getall_bankRcrd } from "../services/server_fetching";
import ViewBankRecords from "./ViewBankRecords";
import ViewVerifyManual from "./ViewVerifyManual";
import ViewCompareComprobante from "./ViewCompareComprobante";
import DeleteAlert from "./deleteAlert";

const Svg = SvgIcon_Cmpt();

const SearchInput = SearchInput_Cmpt();


const CheckBoxSlide = CheckBoxSlide_Cmpt();
const LoadingColorSpinner = LoadingColorSpinner_Cmpt();



const coll2list = "bank_consolidation_2r"

const ViewBankConsoldidation  = ({ data, operations, accountD, subaccountD }) => {
	const [obs, set_obs] = useState(0);
	const [filter, setFilter] = useState(false);
	const [done, setDone] = useState(false);
	const [search, setSearch] = useState("");
	const [searchF, setSearchF] = useState("");
	const [loading, set_loading] = useState(false);

	const [cmpNBnk, setCmpNBnk] = useState(false);

	const [verifyBank, setVerifyBank] = useState(false);


	useEffect(() => {
		updStore("bank_consolidation",null);
		getall_bankRcrd(reCompare);
	},[accountD]);

	const updObs =  () => {
		set_obs(gen16CodeId());
		set_loading(false);
	}

	const filterBc =  (v) => {
		setSearchF(v?v:"");
		let docdata = getStore("bank_consolidation");
		updStore(coll2list,docdata);
		if(search){
			updStore(coll2list,filterBankC(docdata, search, filter, done));
		}
		updObs();
	};

	const changeFilter =  () => {
		let docdata = getStore("bank_consolidation");
		updStore(coll2list,docdata);
		let newfilter = !filter;
		if(search){
			updStore(coll2list,filterBankC(docdata, search, newfilter, done));
		}else if(newfilter){
			updStore(coll2list,filterBankC(docdata, "", newfilter, done));
		}
		setFilter(newfilter);
		updObs();
	};


	const changeFilterType =  () => {
		let docdata = getStore("bank_consolidation");
		updStore(coll2list,docdata);
		let newfilterT = !done;
		if(search){
			updStore(coll2list,filterBankC(docdata, search, filter, newfilterT));
		}else if(filter){
			updStore(coll2list,filterBankC(docdata, "", filter, newfilterT));
		}
		setDone(newfilterT);
		updObs();
	};



	const navigate = useNavigate();

	const navTo =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobanteId}`
		});
		
	}
	

	const exportCSV =  (event) => {
		let yy = getStore(coll2list);
		
		downloadCSVFromJson('comparacion_estados_de_cuentas_' +gen16CodeId()+'.csv',yy)
	}

	const handleChange =  (v) => {
		!loading && set_loading(true);
		setSearch(v)
	}



	const reCompare =  (cb) => {
		let sortBank = getStore("ourBank");
		let submY = prepareSubMayorDatatoCvs(calcSubmayor(operations, accountD?.code,subaccountD?.subAccount),accountD?.code,subaccountD?.subAccount)
		let yy = compareEstadoCuenta(sortBank)
		//downloadCSVFromJson('comparacion_estados_de_cuentas_' +gen16CodeId()+'.csv',yy);
		updStore("bank_consolidation",yy?.ready);
		updStore("cmpbNotInBank",yy?.cmpbNotInBank);
		let r2ad = compareBanks(getStore("sortBank"), getStore("ourBank"));
		if(r2ad.length>0){
			updStore("ourBank2S",r2ad);
			openBankView();
		}
		filterBc();
		updObs();
		if(cb){
			cb && cb();
		}
		

	}



	const importCSV =  (event) => {
		var fileReader = new FileReader();
		fileReader.onload = function(fileLoadedEvent){
			var textFromFileLoaded = fileLoadedEvent.target.result;
			let sb = sortComprobantesByDate(csvJSON(textFromFileLoaded),"Date");
			

			updStore("sortBank",sb);
			reCompare();
		};
	  
		fileReader.readAsText(event.target.files[0], "UTF-8");
	}

	const openBankView =  () => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<ViewBankRecords
				accountD={accountD}
				subaccountD={subaccountD}
			/>
		OpenModal(modal_data);
	}





	return (
		<div style={{
			maxHeight: "80vh",
			width: "90vw",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Consolidacion Bancaria"}
				</p>
				<div className="flexSpace"/>
				<div className="group_title addBtn purple upl">
					Importar CSV (Bank)
					<input id="fileupload" type = "file" onChange={importCSV}/>
				</div>
				<p className="group_title addBtn purple" onClick={exportCSV}>
					Exportar a CSV
				</p>
			</div>

			
			<div className="_dsplFlx " style={{marginTop:14}}>
					<div  style={{marginTop:11}}>
						<SearchInput
							placeholder={"Buscar ...."} 
							width={560}
							obs={set_obs}
							fetchData={filterBc}
							loading={loading}
							change={handleChange}
							callempty={filterBc}
							keyFlds={"search_h65767"}
						/>
					</div>
					
					<div style={{marginLeft:15}}/>
					<div className="_dsplFlx cmp_dtls">
				
						<div className="_dsplFlx"  >
							<div className={"filter_btn _dsplFlx"} onClick={changeFilter}>
								
								<div className="lbl">
								{filter?"Filtrando":"Filtrar"}
								</div>
								<Svg name={filter?"cancel":"filter"} color={"#66666"}/>
							</div>
							{filter?
							<div 
								className={"done_btn _dsplFlx"} 
								style={{borderColor:done?"var(--hrm_palette-success-fg)":"#c62828", marginLeft:9}}
								onClick={changeFilterType}
							>
								
								<Svg name={done?"link":"alert"} color={done?"var(--hrm_palette-mono-filled-hover)":"#c62828"}/>
							</div>
							:null}
							
							
						</div>
					</div>
					<div className="flexSpace"/>
					<div>
						<div className="_dsplFlx " style={{marginTop: 4}}>
							<CheckBoxSlide
								initvalue={verifyBank}
								keyCode={57}
								updChange={(e)=>setVerifyBank(e)}
							/>
							<span className={`checklabel`}>
								{"Registros Verificados"}
							</span>
						</div>
						<div className="_dsplFlx " style={{marginTop: 4}}>
							<CheckBoxSlide
								initvalue={cmpNBnk}
								keyCode={77}
								updChange={(e)=>setCmpNBnk(e)}
							/>
							<span className={`checklabel`}>
								{"Comprobantes fuera del banco"}
							</span>
						</div>
					</div>
					
					<div className="flexSpace"/>
					<div>
						<div className=" addBtn purple btn_purpl_fweight" style={{marginTop:20}} onClick={reCompare}>
							Recalc
						</div>
					</div>
					
				</div>
			
			



				{verifyBank ?
					<ListItemBankVerified  
						search={searchF}
						navTo={navTo}
						reCompare={reCompare}
					/>
				
				: cmpNBnk?
				<div>
					<div className="_dsplFlx hdr_bar purple">
						<div className="tr_bar_15">
							Comprobante
						</div>
						<div className="tr_bar_10">
							Fecha
						</div>
						<div className="tr_bar_10">
							Importe
						</div>
						<div className="tr_bar_10">
							Documento
						</div>
					</div>
					
					<div className="scollVh subMHeight">
					{getStore("cmpbNotInBank") && ObjectKeys(getStore("cmpbNotInBank"))?.map((kEl,ind43) => {
						let element = getStore("cmpbNotInBank")[kEl];

						let amount = element.amount;

						if(amount===0){
							return null;
						}
						return (
							<div className="_dsplFlx hdr_bar" key={ind43}>
								<div className="tr_bar_15 comprobante_id _dsplFlx" >
									<div className="flexSpace"/>
									<div onClick={()=>navTo(element)}>
										{element.comprobanteId}
									</div>
									<div className="flexSpace"/>
								</div>
								<div className="tr_bar_10">
									{element.date && parseDateMMDDYYYY(element.date)}
								</div>
								<div className="tr_bar_10">
									{(amount*1)?.toFixed(2)}
								</div>
								<div className="tr_bar_10">
									{element.document}
								</div>
							</div>
						)
					})}
					</div>
				</div>
				:
				<div>
					<div className="_dsplFlx hdr_bar purple">
						<div className="tr_bar_15">
							Comprobante
						</div>
						<div className="tr_bar_10">
							Fecha
						</div>
						<div className="tr_bar_40">
							Descripcion
						</div>
						<div className="tr_bar_10">
							No cheque
						</div>
						<div className="tr_bar_10">
							Importe
						</div>
						<div className="tr_bar_10">
							Documento
						</div>
					</div>
					<div className="scollVh subMHeight">
					{getStore(coll2list) && getStore(coll2list)?.map((element,ind43) => {
						let show = true;
						let rangDt = getStore("date_range_submayor");

						if(filter){
							show = false;
							if(done && element.comprobanteId){
								show = true;
							}
							if(!done && !element.comprobanteId){
								show = true;
							}
						}

						if(!show || isNaN(element.amount)){
							return null;
						}

						
						if(element.date<rangDt?.initDate || element.date>rangDt?.lastDate){
							return null
						}
					
						
						return(
							<ItemBank 
								element={element}
								inx={ind43}
								key={ind43+"_bankItm"}
								navTo={navTo}
								reCompare={reCompare}
							/>
						) 
				})}
				</div>
			</div>
			}
		</div>
	);
};




export default ViewBankConsoldidation;





const ListItemBankVerified = ({searchQry, navTo, reCompare, search })=>{

	const [loading, set_loading] = useState(false);

	const doneConfirmLink =  () => {
		set_loading(false)
	}



	return (
		<div>
				<div className="_dsplFlx hdr_bar purple">
					<div className="tr_bar_15">
						Comprobante
					</div>
					<div className="tr_bar_10">
						Fecha
					</div>
					<div className="tr_bar_40">
						Descripcion
					</div>
					<div className="tr_bar_10">
						No cheque
					</div>
					<div className="tr_bar_10">
						Importe
					</div>
					<div className="tr_bar_10">
						Documento
					</div>
				</div>
				<div className="scollVh subMHeight">
				{getStore("ourBank_filtered")?.verified && getStore("ourBank_filtered")?.verified?.map((element,ind43) => {

					if(element.status!==9){
						return null;
					}
					
					if(search){
						if(!compareSearch(element,search)){
							return null;
						}
					}
					
					return(
						<ItemBank 
							element={element}
							inx={ind43}
							key={ind43+"_bankItm"}
							navTo={navTo}
							reCompare={reCompare}
							verifyManual={true}
						/>
					) 
			})}
			</div>
		</div>
	)

}



const ItemBank = ({element, navTo, reCompare, inx, verifyManual })=>{

	const [loading, set_loading] = useState(false);

	const doneConfirmLink =  () => {
		set_loading(false)
	}
	

	const send2Compare =  () => {
		reCompare(doneConfirmLink)
	}


	const openVerifyManual =  () => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<ViewVerifyManual  item={element} confirm={send2Compare}  />
		OpenModal(modal_data);
	}

	
	const openVerifyCompare =  () => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<ViewCompareComprobante  item={element} confirm={send2Compare} />
		OpenModal(modal_data);
	}

	const confirmLink = async (element) => {
		if(verifyManual){
			openVerifyCompare();
		}
		else if(!element.linkId && element.operationId){
			openVerifyCompare();
		}else{
			openVerifyManual();
		}
	}



	const confirmDelete = async  (v)  => {
		let bdyq2 = {...getStore(qryStore())?.delete_bank_bUp};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId,
			bankRecordId: element.bankRecordId
		}
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			getall_bankRcrd(send2Compare);
		}
	};



	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert 
				confirm={confirmDelete}
				lbl_p={"Eliminar Movimiento del Banco"} 
				lbl_span={"Una vez eliminado no podra ser recuperado"}
			/>
			OpenModal(modaldata);
		}
	}
	


	return(
		<div className="_dsplFlx hdr_bar ">

			
			<div className="tr_bar_15 comprobante_id _dsplFlx" >
				{loading?
				<div className="icon_compr" >
						<LoadingColorSpinner stroke={"#646cff"} size={24} strokeW={8}/>
					</div>
				:<div className="icon_compr" onClick={()=>confirmLink(element)}>
					<Svg 
						name={element.linkId ? "check_circle":  element.comprobanteId?"link":"alert"} 
						color={element.linkId ?"var(--hrm_palette-success-fg)": element.comprobanteId?"var(--hrm_palette-mono-filled-hover)":"#c62828"}
					/>
				</div>
				}
				
				<div className="flexSpace"/>
				<div onClick={()=>navTo(element)}>
					{element.comprobanteId}
				</div>
				<div className="flexSpace"/>
			</div>
			<div className="tr_bar_10">
				{element.date && parseDateMMDDYYYY(element.date)}
			</div>
			<div className="tr_bar_40 desc_font">
				{element.description}
			</div>
			<div className="tr_bar_10">
				{element.checkOrSlip}
			</div>
			
			<div className="tr_bar_10">
				{(element.amount*1)?.toFixed(2)}
			</div>
			<div className="tr_bar_10">
				{element.document}
			</div>
			{element.linkId?
				null
			:
			<div className="icon_2rmv" onClick={OpenDeleteAlert}>
				<Svg 
					name={"outline_delete"} 
					color={"#c62828"}
				/>
			</div>
			}
		</div>
	) 

}






function filterBankC(arr1, qry, filt, isDone) {
	let srch = qry.toLowerCase();
    var newArr = [];
	ObjectKeys(arr1)?.map((s) => {
		let itm = arr1[s];
		let isValid = false;
		let hasComp = isDone && itm["comprobanteId"] || !isDone && !itm["comprobanteId"];
		let isOk = hasComp && itm["status"] === 4;
		if(srch){
			isValid = false;
			["description", "amount","checkOrSlip", "comprobanteId"].map(fld=>{
				if(filt){
					if(itm[fld] && itm[fld]?.toString()?.toLowerCase()?.indexOf(srch)>=0 && isOk){
						isValid = true;
					}
				}
				else{
					if( itm[fld] && itm[fld]?.toString()?.toLowerCase()?.indexOf(srch)>=0){
						isValid = true;
					}
				}
				
			})
		}
		else if(isOk){
			isValid = true;
		}
		if (isValid) {
			newArr.push(itm);
		}
	});
	
	return newArr;
}





const compareSearch = (itm,qry) =>{
	let srch = qry.toLowerCase();
	let isValid = false;
	["description", "amount","document", "comprobanteId"].map(fld=>{
		if( itm[fld] && itm[fld]?.toString()?.toLowerCase()?.indexOf(srch)>=0){
			isValid = true;
		}
	})
	return isValid;
}



