import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, downloadCSVFromJson, isSuperUser, updStore, ObjectKeys, csvJSON, qryStore, fetchEncryptStream } from "../../../stores/Utils";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt, SearchInput_Cmpt, CheckBoxSlide_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";
import { fetchItemOnQueue, getall_bankRcrd } from "../services/server_fetching";

import LinkAlert from "./LinkAlert";


const Svg = SvgIcon_Cmpt();

const SearchInput = SearchInput_Cmpt();
const LoadingColorSpinner = LoadingColorSpinner_Cmpt();




const coll2list = "ourBank2S"

const ViewVerifyManual  = ({ data, item }) => {
	const [obs, set_obs] = useState(0);
	const [filter, setFilter] = useState(false);
	const [done, setDone] = useState(false);
	const [search, setSearch] = useState("");

	const [loading, set_loading] = useState(false);

	const [loadingSave, set_loadingSave] = useState(false);



	const updObs =  () => {
		set_obs(gen16CodeId());
		set_loading(false);
	}

	const filterBc =  (v) => {
		
		setSearch(v)
		loading && set_loading(false);
	};
	const navigate = useNavigate();

	const handleChange =  (v) => {
		!loading && set_loading(true);
		setSearch(v)
	}








	const closeView =  () => {
		set_loadingSave(false);
		CloseModal({id:data.modalID});
	};



	const LinkModalAlert =  (element) => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<LinkAlert  
				item={element}
				lbl_p={"Associar con el banco"} 
				lbl_span={`esta seguro que desea associar el comprobante ${element.comprobanteId} a la cuenta de banco`} 
				lbl_btn={"Confirm"}
				theme={"purple"}
				confirm={()=>confirmLink(element)}
			/>
		OpenModal(modal_data);
	}


	
	const confirmLink = async (element) => {

			let bdyq2 = {...getStore(qryStore())?.upd_bank_bUp};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId,
				bankRecordId: item.bankRecordId
			}
			bdyq2.data2update={
				linkId: element.operationId, 
				document: element.document, 
				comprobanteId: element.comprobanteId,
				status: 9
			};
			

			const res = fetchEncryptStream(bdyq2);
			const td = await res;

    		// console.log(td);
    
			if (td) {
				getall_bankRcrd(closeView);
			}
		
	}





	return (
		<div style={{
			maxHeight: "80vh",
			width: "90vw",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Associar comprobante Manual"}
				</p>
				<div className="flexSpace"/>
				
			</div>



			<div className="_dsplFlx " style={{marginTop:14}}>
					<div  style={{marginTop:11}}>
						<SearchInput
							placeholder={"Buscar ...."} 
							width={560}
							obs={set_obs}
							fetchData={filterBc}
							loading={loading}
							change={handleChange}
							callempty={filterBc}
							keyFlds={"search_h65767"}
						/>
					</div>
				</div>
			
			


				<div  style={{marginTop:14}}>

					

					<div className="_dsplFlx hdr_bar green" style={{margin:"6px 0"}}>
						
						<div className="tr_bar_15">
							{item.date && parseDateMMDDYYYY(item.date)}
						</div>
						<div className="tr_bar_15">
							{item.comprobanteId}
						</div>
						<div className="tr_bar_40 desc_font">
							{item.description}
						</div>
						<div className="tr_bar_15">
							{item.checkOrSlip}
						</div>
						<div className="tr_bar_15">
							{(item.amount*1)?.toFixed(2)}
						</div>
					</div>
					<div className="_dsplFlx hdr_bar purple">
						
						<div className="tr_bar_15">
							Fecha
						</div>
						<div className="tr_bar_15">
							Comprobante
						</div>
						<div className="tr_bar_40">
							Descripcion
						</div>
						<div className="tr_bar_15">
							No cheque
						</div>
						<div className="tr_bar_15">
							Importe
						</div>
					</div>
					<div className="scollVh subMHeight">
					{getStore("submayor_list") && ObjectKeys(getStore("submayor_list"))?.map((eleId,ind43) => {
						//console.log(element)
						let element = getStore("submayor_list")?.[eleId];
						
						
						if(search){
							if(!compareSearch(element,search)){
								return null;
							}
						}
						
						

						return(
							<div className="_dsplFlx hdr_bar manual_vrfy" key={ind43}>

								
								<div className="tr_bar_15">
									{element.date && parseDateMMDDYYYY(element.date)}
								</div>
								<div className="tr_bar_15 comprobanteId"  onClick={()=>LinkModalAlert(element)}>
									{element.comprobanteId}
								</div>
								<div className="tr_bar_40 desc_font">
									{element.description}
								</div>
								<div className="tr_bar_15">
									{element.document}
								</div>
								
								<div className="tr_bar_15">
									{(element.amount*1)?.toFixed(2)}
								</div>
							</div>
						) 
				})}
				</div>
			</div>
		</div>
	);
};




export default ViewVerifyManual;



const compareSearch = (itm,qry) =>{
	let srch = qry.toLowerCase();
	let isValid = false;
	["description", "amount","document", "comprobanteId"].map(fld=>{
		if( itm[fld] && itm[fld]?.toString()?.toLowerCase()?.indexOf(srch)>=0){
			isValid = true;
		}
	})
	return isValid;
}