import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, downloadCSVFromJson, isSuperUser, updStore, ObjectKeys, csvJSON, qryStore } from "../../../stores/Utils";
import { useNavigate } from "react-router";
import { CloseModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt, SearchInput_Cmpt, CheckBoxSlide_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";
import { fetchItemOnQueue } from "../services/server_fetching";

const Svg = SvgIcon_Cmpt();

const SearchInput = SearchInput_Cmpt();
const LoadingColorSpinner = LoadingColorSpinner_Cmpt();




const coll2list = "ourBank2S"

const ViewBankRecords  = ({ data, operations, accountD, subaccountD }) => {
	const [obs, set_obs] = useState(0);
	const [filter, setFilter] = useState(false);
	const [done, setDone] = useState(false);
	const [search, setSearch] = useState("");

	const [loading, set_loading] = useState(false);

	const [loadingSave, set_loadingSave] = useState(false);

	const [cmpNBnk, setCmpNBnk] = useState(false);

	useEffect(() => {
	},[accountD]);

	const updObs =  () => {
		set_obs(gen16CodeId());
		set_loading(false);
	}

	const filterBc =  () => {
		
		updObs();
	};

	const changeFilter =  () => {
	
	};


	const changeFilterType =  () => {
		
	};



	const navigate = useNavigate();

	const navTo =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobante}`
		});
		
	}
	

	const exportCSV =  (event) => {
	}

	const handleChange =  (v) => {
		!loading && set_loading(true);
		setSearch(v)
	}



	const reCompare =  (v) => {
		//let nn = getStore("ourBank") || [];
		//const all = [...nn, ...getStore(coll2list)];
		//updStore("ourBank",all);
		set_loadingSave(true);
		let arr2S = []
		getStore(coll2list).map(itm2=>{
			let itm = {...itm2};
			itm["status"] = 4;
			let bdyq2 = {...getStore(qryStore())?.add_bank_bUp};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form = itm;
			arr2S.push(bdyq2);
		})
		//console.log(arr2S)
		fetchItemOnQueue(arr2S, closeView);
		//CloseModal({id:data.modalID});
	}





	const closeView =  () => {
		set_loadingSave(false);
		CloseModal({id:data.modalID});
	};


	return (
		<div style={{
			maxHeight: "80vh",
			width: "90vw",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Registros del Banco"}
				</p>
				<div className="flexSpace"/>
				
				<p className="group_title addBtn purple" onClick={exportCSV}>
					Exportar a CSV
				</p>
			</div>

			
			<div className="_dsplFlx " style={{marginTop:14}}>
					<div  style={{marginTop:11}}>
						<SearchInput
							placeholder={"Buscar ...."} 
							width={560}
							obs={set_obs}
							fetchData={filterBc}
							loading={loading}
							change={handleChange}
							callempty={filterBc}
							keyFlds={"search_h65767"}
						/>
					</div>
					
					<div style={{marginLeft:15}}/>
					<div className="_dsplFlx cmp_dtls">
				
						<div className="_dsplFlx"  >
							<div className={"filter_btn _dsplFlx"} onClick={changeFilter}>
								
								<div className="lbl">
								{filter?"Filtrando":"Filtrar"}
								</div>
								<Svg name={filter?"cancel":"filter"} color={"#66666"}/>
							</div>
							{filter?
							<div 
								className={"done_btn _dsplFlx"} 
								style={{borderColor:done?"var(--hrm_palette-success-fg)":"#c62828", marginLeft:9}}
								onClick={changeFilterType}
							>
								
								<Svg name={done?"check_circle":"alert"} color={done?"var(--hrm_palette-success-fg)":"#c62828"}/>
							</div>
							:null}
							
							
						</div>
					</div>
					
					<div className="flexSpace"/>
					<div>
					{loadingSave?
						<div className="icon_compr" >
							<LoadingColorSpinner stroke={"#646cff"} size={38} strokeW={6}/>
						</div>
						:
						<div className=" addBtn purple btn_purpl_fweight" style={{marginTop:20}} onClick={reCompare}>
							Save
						</div>
					}
					</div>
					
				</div>
			
			


				<div>
					<div className="_dsplFlx hdr_bar purple">
						
						<div className="tr_bar_20">
							Fecha
						</div>
						<div className="tr_bar_40">
							Descripcion
						</div>
						<div className="tr_bar_20">
							No cheque
						</div>
						<div className="tr_bar_20">
							Importe
						</div>
					</div>
					<div className="scollVh subMHeight">
					{getStore(coll2list) && getStore(coll2list)?.map((element,ind43) => {
						//console.log(element)
						
						return(
							<div className="_dsplFlx hdr_bar" key={ind43}>

								
							
								<div className="tr_bar_20">
									{element.date && parseDateMMDDYYYY(element.date)}
								</div>
								<div className="tr_bar_40 desc_font">
									{element.description}
								</div>
								<div className="tr_bar_20">
									{element.checkOrSlip}
								</div>
								
								<div className="tr_bar_20">
									{(element.amount*1)?.toFixed(2)}
								</div>
							</div>
						) 
				})}
				</div>
			</div>
		</div>
	);
};




export default ViewBankRecords;
