import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, downloadCSVFromJson, isSuperUser, updStore, parseFloatToDecimal } from "../../../stores/Utils";
import { get_filter_by_acc, calcSubmayor, prepareSubMayorDatatoCvs, getPrevBalance, getall_bankRcrd } from "../services/server_fetching";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import ViewBankConsoldidation from "./ViewBankConsoldidation";
import { MonthPicker_Cmpt } from "../../../components";


const MonthPicker = MonthPicker_Cmpt();

const ViewComprobante  = ({ data, accountForm }) => {
	const [obs, set_obs] = useState(0);
	

	let item = getStore("submayor_group");
	
	useEffect(() => {
		get_filter_by_acc(accountForm,updObs);
	},[accountForm]);

	const updObs =  () => {
		set_obs(gen16CodeId())
	}


	let accountD = getStore("account_ids")?.[accountForm?.account];
	let subaccountD = getStore("sub_account_ids")?.[accountForm?.subAccount];

	const navigate = useNavigate();

	const close =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobanteId}`
		});
		
	}
	

	const openCalendarModalPicker =  () => {
		downloadCSVFromJson('export_submayor_' +accountD?.code+"_"+(subaccountD?.subAccount? subaccountD?.subAccount+"_" : "") +gen16CodeId()+'.csv',prepareSubMayorDatatoCvs(calcSubmayor(item?.operations, accountD?.code,subaccountD?.subAccount),accountD?.code,subaccountD?.subAccount))

	}


	const openBankCon =  () => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<ViewBankConsoldidation 
				accountD={accountD}
				subaccountD={subaccountD}
				operations={item?.operations}
			/>
		OpenModal(modal_data);
		CloseModal({id:data.modalID});
	}




	const handlePickMonth =  ({initDate,lastDate}) => {

		updStore("date_range_submayor", {initDate,lastDate} );
		get_filter_by_acc(accountForm,updObs);
	}


	let monthInit = (new Date(getStore("date_range_submayor")?.initDate)).getMonth();

	let prevBalance = getPrevBalance(monthInit,accountForm);
	
	return (
		<div style={{
			maxHeight: "80vh",
			width: "960px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect"  >
					{ "SubMayor de la Cuenta"}
				</p>
				
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={openCalendarModalPicker}>
					Exportar a CSV
				</p>
				{isSuperUser() && getStore("global_account_params")?.cash_bank?.account ===  accountD?.accountId ?
					<div className="group_title addBtn purple upl" onClick={openBankCon}>
						Consolidacion Bancaria
					</div>
				:null}
			</div>
		


			
			
			
			<div className="_dsplFlx cmp_dtls">
				<div className=" acc_dtls" style={{marginTop: 10}}>
					<div className="_dsplFlx"  >
						<div className="code" data-ref={`${accountD?.accountId}`} style={{color: "var(--hrm_palette-blue-purple)"}}>
							{accountD?.code} 
							
							
						</div>
						<div className="name" style={{color:"var(--hrm_palette-imput-label_gray)"}}>
							{accountD?. name}
						</div>
					</div>
					<div className="flexSpace"/>
					{subaccountD ?
					<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						<div className="code"  data-ref={`${subaccountD?.sub_accountId}`} style={{color:"var(--hrm_palette-inverse-background-b)"}} >
							{subaccountD?.subAccount} 
						</div>
						<div className="name"  style={{color:"var(--hrm_palette-imput-label_gray)"}}>
							{subaccountD?. subAccountName}
						</div>
				   	</div>
					:null}
				</div>
				<div className="flexSpace"/>
				<div className="_dsplFlx acc_dtls" style={{margin: "18px 10px 0 0"}} >
					<div className="name"  style={{color:"var(--hrm_palette-imput-label_gray)"}}>
						Saldo Inicial
					</div>
					<div className="code" style={{ marginLeft: 10,color: "var(--hrm_palette-blue-purple)"}}>
							{accountD.type === "credit" ?  (prevBalance *-1).toFixed(2): (prevBalance *1).toFixed(2) }
					</div>
				</div>
				<div className="_dsplFlx"  >
					<div style={{marginTop: 6}}>
						<MonthPicker 
							onSelect={handlePickMonth} 
							initMonth={monthInit} 
							direction={"right"}
						/>
					</div>
				</div>
			</div>




			
			<div className="_dsplFlx hdr_bar purple">
				<div className="tr_bar_s6">
					Comprobante
				</div>
				<div className="tr_bar_s6">
					Fecha
				</div>
				<div className="tr_bar_s6">
					Documento
				</div>
				<div className="tr_bar_s6">
					Debito
				</div>
				<div className="tr_bar_s6">
					Credito
				</div>
				<div className="tr_bar_s6">
					Saldo
				</div>
			</div>

			<div className="scollVh subMHeight">
			{item?.operations && calcSubmayor(item?.operations, accountForm?.account, accountForm?.subAccount, monthInit)?.map((element,ind43) => {
				return(
					<div className="_dsplFlx hdr_bar" key={ind43}>
						<div className="tr_bar_s6 comprobante_id" onClick={()=>close(element)}>
								{element.comprobanteId}
						</div>
						<div className="tr_bar_s6">
							{element.date && parseDateMMDDYYYY(element.date)}
						</div>
						<div className="tr_bar_s6">
							{element.document}
						</div>
						<div className="tr_bar_s6">
							{(element.debit*1)?.toFixed(2)}
						</div>
						<div className="tr_bar_s6">
							{(element.credit*1)?.toFixed(2)}
						</div>
						<div className="tr_bar_s6">
							{(element.saldo*1)?.toFixed(2)}
						</div>
					</div>
				)
			})}
			</div>
		</div>
	);
};

export default ViewComprobante;



