

import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, downloadCSVFromJson, isSuperUser, updStore, ObjectKeys, csvJSON, qryStore, fetchEncryptStream } from "../../../stores/Utils";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt, SearchInput_Cmpt, CheckBoxSlide_Cmpt, LoadingColorSpinner_Cmpt } from "../../../components";
import { getall_bankRcrd } from "../services/server_fetching";
import ViewVerifyManual from "./ViewVerifyManual";


const Svg = SvgIcon_Cmpt();

const SearchInput = SearchInput_Cmpt();
const LoadingColorSpinner = LoadingColorSpinner_Cmpt();




const coll2list = "ourBank2S"

const ViewCompareComprobante  = ({ data, item, confirm }) => {
	const [obs, set_obs] = useState(0);
	
	const [loadingSave, set_loadingSave] = useState(false);



	const updObs =  () => {
		set_obs(gen16CodeId());
	}


	const closeView =  () => {
		set_loadingSave(false);
		CloseModal({id:data.modalID});
		confirm && confirm();
	};



	let cmpId =  item.operationId || item.linkId;




	let regComprobante = getStore("submayor_list")?.[cmpId];


	let hasAssociateOption =  regComprobante || !item.linkId || ( item.operationId &&  item.operationId !==item.linkId);

	
	const confirmLink = async (element) => {
			set_loadingSave(true);
			let bdyq2 = {...getStore(qryStore())?.upd_bank_bUp};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId,
				bankRecordId: item.bankRecordId
			}

					
			bdyq2.data2update={
				linkId: element.operationId, 
				document: element.document, 
				comprobanteId: element.comprobanteId,
				status: 9
			};
			

			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			//console.log(bdyq2)
    		//console.log(td);
			if (td) {
				getall_bankRcrd(closeView);
			}
		
	}


	
		
	const openVerifyManual =  () => {
		
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<ViewVerifyManual  item={item}  />
		OpenModal(modal_data);
		closeView();
	}


	return (
		<div style={{
			maxHeight: "80vh",
			width: "860px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Comparar Comprobante con el Banco"}
				</p>
				<div className="flexSpace"/>
				<div className="group_title addBtn purple upl" onClick={openVerifyManual}>
					Manual
				</div>
				{hasAssociateOption?
					loadingSave?
					<div className="icon_compr _dsplFlx" style={{marginTop:12}} >
						<div className="lbl_saving">
								Linking ... 
						</div>
						<LoadingColorSpinner stroke={"#646cff"} size={24} strokeW={8}/>
					</div>
					:
					<div className="group_title addBtn purple upl" onClick={()=>confirmLink(item)}>
						Associar
					</div>
					:null
				}
			</div>



		

			<div className="_dsplFlx  spaceAround flxWrp"  style={{marginTop:24}}>

				<div className={"boxCmp2compare green"}>
					<div className="boxCmp2compare_title">
						Registro del Banco
					</div>
					<div  className="boxCmp2compare_body">
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Fecha: 
								</div>
								<div className="value">
									{item.date && parseDateMMDDYYYY(item.date)}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Importe: 
								</div>
								<div className="value">
								${item.amount}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									No Cheque: 
								</div>
								<div className="value">
								{item.checkOrSlip}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Comprobante: 
								</div>
								<div className="value">
									{item.comprobanteId}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="_dsplFlx">
								<div className="value small">
									{item.description}
								</div>
							</div>
						</div>
						
					</div>
					
				</div>
			
			

			
				<div className={"boxCmp2compare purple"}>
					<div className="boxCmp2compare_title">
						Comprobante en Contabilidad
					</div>
					<div  className="boxCmp2compare_body">
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Fecha: 
								</div>
								<div className="value">
									{regComprobante?.date && parseDateMMDDYYYY(regComprobante?.date)}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Importe: 
								</div>
								<div className="value">
								${regComprobante?.amount}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Documento: 
								</div>
								<div className="value">
								{regComprobante?.document}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="flexSpace"/>
							<div className="_dsplFlx">
								<div className="lbl">
									Comprobante: 
								</div>
								<div className="value">
									{regComprobante?.comprobanteId}
								</div>
							</div>
						</div>
						<div className="_dsplFlx">
							<div className="_dsplFlx">
								<div className="value small">
									{regComprobante?.description}
								</div>
							</div>
						</div>
						
					</div>
					
				</div>
			
			</div>
		</div>
	);
};




export default ViewCompareComprobante;



const compareSearch = (itm,qry) =>{
	let srch = qry.toLowerCase();
	let isValid = false;
	["description", "amount","document", "comprobanteId"].map(fld=>{
		if( itm[fld] && itm[fld]?.toString()?.toLowerCase()?.indexOf(srch)>=0){
			isValid = true;
		}
	})
	return isValid;
}